.wrapper {
  width: 90%;
  margin-left: 80px;
  min-width: 590px;
  position: absolute;
  display: flex;
  justify-content: center;
  filter: drop-shadow(3px 3px 3px #222);
}

.container {
  width: 100%;
  padding: 30px 30px 50px;
  background-color: #6ab0d8;
  border-radius: 10px;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2);
}

.container * {
  font-family: "Poppins", sans-serif;
  color: black;
  font-weight: 500;
}

h2 {
  margin-bottom: 50px;
  letter-spacing: 2px;
  text-align: center;
  font-size: 33px;
  font-weight: bold;
}

.details {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.bar {
  position: relative;
  border: 2px solid #0d96e0;
  border-radius: 20px;
}

.bar div {
  position: relative;
  width: 0;
  height: 9px;
  border-radius: 10px;
  background-color: #0a0a0a;
}

.skills:not(:last-child) {
  margin-bottom: 30px;
}

#htmlbar {
  animation: html-fill 2s forwards;
}

@keyframes html-fill {
  100% {
    width: 90%;
  }
}

#cssbar {
  animation: css-fill 2s forwards;
}

@keyframes css-fill {
  100% {
    width: 75%;
  }
}

#javascriptbar {
  animation: js-fill 2s forwards;
}

@keyframes js-fill {
  100% {
    width: 92%;
  }
}

#jQuerybar {
  animation: jQuery-fill 2s forwards;
}

@keyframes jQuery-fill {
  100% {
    width: 88%;
  }
}